import React from 'react';
import './Register.css'

const Register = ({ onRouteChange }) =>  {
    return(
        <article className="br3 ba b--black-10 mv6 w-50-m w-25-l center">
            <main className="pa5 black-80 w-90">
                <div className="measure">
                    <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
                        <legend className="f1 fw6 ph0 mh0 tc">Register</legend>
                        <div className="mt3">
                            <label className="db fw6 lh-copy f6" htmlFor="user-name">Name</label>
                            <input className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="text" name="user-name"  id="user-name" />
                        </div>
                        <div className="mt3">
                            <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
                            <input className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="email" name="email-address"  id="email-address" />
                        </div>
                        <div className="mv3">
                            <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
                            <input className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="password" name="password"  id="password" />
                        </div>
                    </fieldset>
                    <div className="center">
                        <input className="ph3 pv2 input-reset ba2 b--black bg-transparent grow pointer f6 fw9" 
                               type="submit" value="Register" 
                               onClick={ () => onRouteChange('home') } />
                    </div>
                </div>
            </main>
        </article>
    );
}

export default Register;